import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { upsertIssue } from "../../store/Issue";
import PropTypes from "prop-types";
import { hide } from "../../store/Modal";
import styled from "styled-components";
import { Button } from "../atoms/Button";
import { TextField } from "../atoms/TextField";
import { TextArea } from "../atoms/TextArea";

const SContainer = styled.div`
  max-width: 598px;
  margin: auto;
`;

const STitle = styled.h2`
  padding: 0 8px;
`;

const SForm = styled.div`
  padding: 32px 0;
  padding-bottom: 16px;
`;
const SField = styled.div`
  padding: 16px;
`;
const SLabel = styled.label`
  display: block;
  padding: 8px 0;
`;

const SErrorMessageContainer = styled.div`
  padding: 16px;
  min-height: 100px;
`;

const SErrorMessage = styled.p`
  color: rgb(215, 58, 73);
  background: #d73a4959;
  padding: 8px;
  border-radius: 6px;
`;

const SFooter = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 8px;
`;

export const IssueForm = (props) => {
  const { id } = props || {};
  const issue = useSelector((state) => state.issues[id]);
  const isEdit = !!issue;
  const [validationError, setValidationError] = useState("");
  const dispatch = useDispatch();

  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [status, setStatus] = useState(0);

  useEffect(() => {
    if (isEdit) {
      setTitle(issue.title);
      setDescription(issue.description);
      setStatus(issue.status);
      setValidationError("");
    }
  }, []);

  const onSubmit = () => {
    if (!title) {
      setValidationError("");
      return;
    }

    if (!description) {
      setValidationError("");
      return;
    }

    dispatch(upsertIssue({ id, title, description, status }));
    dispatch(hide());
  };

  const onClose = () => {
    dispatch(hide());
  };

  return (
    <SContainer>
      <STitle>Issue</STitle>
      <SForm>
        <SField>
          <SLabel></SLabel>
          <TextField
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            placeholder=""
          />
        </SField>
        <SField>
          <SLabel></SLabel>
          <TextArea
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            placeholder=""
          />
        </SField>
      </SForm>
      {isEdit ? (
        <SField>
          <SLabel></SLabel>
          <select value={status} onChange={(e) => setStatus(e.target.value)}>
            <option value={0}>Open</option>
            <option value={1}>Close</option>
          </select>
        </SField>
      ) : null}
      <SErrorMessageContainer>
        {validationError && <SErrorMessage>{validationError}</SErrorMessage>}
      </SErrorMessageContainer>
      <SFooter>
        <Button
          variant="primary"
          onClick={onSubmit}
          text={isEdit ? "" : ""}
        />
        <Button onClick={onClose} text="" />
      </SFooter>
    </SContainer>
  );
};

IssueForm.propTypes = {
  isOpen: PropTypes.bool,
  onRequestClose: PropTypes.func,
};
